<script>
import hxReturn from "../components/hxReturn";
import hxRecommended from "../components/hxRecommended";

import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("cart");

export default {
  data() {
    return {
      value: 1,
      isshow: false,
      show: false,
      No: 1,
      specificationslist: ["套餐一", "套餐二", "套餐三"],
      check: true,
      like: false,
      // id: "20200530",
      // title:
      //   "英特尔酷睿i5 9400f/i7组装电脑主机独显游戏台式机家用电竞吃鸡英雄联盟LOL台式电脑整机全套",
      // price: "3999",
      num: 1,
      // url: require("../assets/images/hometuijian01.jpg"),
      specifications: "套餐一"
    };
  },

  methods: {
    // 修改为喜欢
    modifylike() {
      if (this.$store.state.login == 0) {
        this.$toast("请登入");
        return;
      }
      // console.log(this.id)
      let id = this.id.substring(0, 8);
      this.$store.commit("modifylike", id);
      this.like = true;
    },
    // 改为不喜欢
    Nolike() {
      let id = this.id.substring(0, 8);
      this.$store.commit("Nolike", id);
      this.like = false;
    },
    // 加入购物车
    add() {
      if (this.$store.state.login == 0) {
        this.$toast("请先登入");
        this.$router.push({name:"mylogin"})
        return;
      }
      let obj = {
        check: this.check,
        id: this.id,
        title: this.title,
        price: this.price,
        num: this.num,
        url: this.url,
        specifications: this.specifications
      };
      this.$store.commit("cart/add", obj);
      this.isshow = false;
      this.num = 1;
    },
    // 修改商品规格
    Nos(item, index) {
      this.No = index+1;
      this.specifications = item;
      this.id = this.id + "" + (this.No + "");
    },
    // 重置num
    setnum() {
      this.num = 1;
    },
    // 不加入购物车直接购买
    Add() {
      if (this.$store.state.login == 0) {
        this.$toast("请先登入");
        this.$router.push({name:"mylogin"})
        return;
      }
      let obj = [
        {
          num: this.num,
          price: this.price,
          specifications: this.specifications,
          title: this.title,
          url: this.url
        }
      ];
      this.$store.state.tobepaidlist = { obj: obj, price: this.price * 100 };
      this.$router.push({ name: "placeorder" });
    }
  },
  computed: {
    ...mapState({
      nums: state => state.nums
    })
  },
  created() {
    //隐藏导航
    this.$store.state.vantTab = false;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    // console.log(this.$route.query)
    // 数据更新
    this.url = this.$route.query.url;
    this.title = this.$route.query.title;
    this.price = this.$route.query.price;
    this.id = this.$route.query.id;
    this.like = this.$route.query.like;
    // console.log(this.like)
  },
  watch: {
    $route(to) {
      // console.log(to.query);
      this.url = to.query.url;
      this.title = to.query.title;
      this.price = to.query.price;
      this.id = to.query.id;
    }
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    // this.$store.state.vantTab = true;
  },
  components: {
    hxReturn,
    hxRecommended
  }
};
</script>

